body {
  margin: 0;
  padding: 0;
  font-family: 'Caveat', cursive;;
}

.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.grid-container {
    font-family: 'Caveat', cursive;
}
.text-center.header, .small-10.cell {
    font-family: 'Montserrat', sans-serif;
    margin-bottom: 40px;
}

.infoBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #2979FF;
    border-radius: 4px 4px 0 0;
    height: 60px;
    width: 100%;
  }
  
  .leftInnerContainer {
    flex: 0.5 1;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: white;
  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5 1;
    justify-content: flex-end;
    margin-right: 5%;
  }
  
  .onlineIcon {
    margin-right: 5%;
  }
.messageBox {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }
  
  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  
  .messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
  }
  
  .pl-10 {
    padding-left: 10px;
  }
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    justify-content: flex-start;
  }
  
  .justifyEnd {
    justify-content: flex-end;
  }
  
  .colorWhite {
    color: white;
  }
  
  .colorDark {
    color: #353535;
  }
  
  .backgroundBlue {
    background: #2979FF;
  }
  
  .backgroundLight {
    background: #F3F3F3;
  }
.messages {
    padding: 5% 0;
    overflow: auto;
    flex: auto;
  }
 
  
.form {
    display: flex;
    border-top: 2px solid #D3D3D3;
  }
  
  .input {
    border: none;
    border-radius: 0;
    padding: 5%;
    width: 80%;
    font-size: 1.2em;
    font-family: 'Montserrat', sans-serif;
  }
  
  input:focus, textarea:focus, select:focus{
    outline: none;
  }
  
  .sendButton {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #2979FF;
    padding: 20px;
    display: inline-block;
    border: none;
    width: 20%;
  }
.container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #fff;
  border-radius: 8px;
  min-height: 100vh;
}

.messageWrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #fff;
  border-radius: 8px;
  height: 500px;
}
.footer-content{
    display: flex;
    
    height: 100%;
}

.footer{
    background: #5b5656 ;
    color: azure;
    position: relative;
}
.footer .footer-bottom{
    background:#4d4646;
    color: white;
    width: 100%;
    text-align: center;
    padding: 20px;
    font-size: 2rem;
}
.footer-section{
    flex: 1 1;
    padding: 25px;
    
    /* border: 1px solid white; */
}

.logo{
    align-content: center;
    margin: auto;
    max-height: 90px;
}



.footerTitle{
    font-size: 35px;
    /* color:#7fcd91;; */
    font-weight: bold;
    text-align: center;
}



.enlarge:hover{
  transform: scale(1.5);
 
  
}
.top-bar, .nav, .horizontal.menu.top-bar-right.nav {
    color: white;
    background-color: black;
    font-family: 'Caveat', cursive;
}

